import React, { useEffect } from 'react';
import { Box, Typography, Grid, Paper } from '@mui/material';
import { useState } from 'react';

export const PVdesign = ({ pvOption }) => {
    const [totalPrice, setTotalPrice] = useState(0);
    const [totalPower, setTotalPower] = useState(0);
    const [totalPanelsWeights, setTotalPanelsWeights] = useState(0);
    const [inverter, setInverter] = useState(null);
    const [panel, setPanel] = useState(null);
    // eslint-disable-next-line no-unused-vars
    const [charge, setCharge] = useState(null);
    // eslint-disable-next-line no-unused-vars
    const [additionalElements, setAdditionalElements] = useState(null);

    useEffect(() => {
        if (pvOption) {
            setTotalPrice(pvOption.total_price);
            setTotalPower(pvOption.total_power_kW);
            setTotalPanelsWeights(pvOption.total_panel_weight_kg);
            setInverter(pvOption.inverter);
            setPanel(pvOption.panel);
            if (pvOption.charge) {
                setCharge(pvOption.charge)
            }

        }
    }, [pvOption])

    return (
        <Box display="flex" flexDirection="column" gap={1}>
            <Typography variant="h5" gutterBottom>
                 
            </Typography>
            <Grid container spacing={2}>
                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="subtitle1"> :</Typography>
                        <Typography>{totalPower || ' '} </Typography>
                        <Typography variant="subtitle1"> :</Typography>
                        <Typography>{totalPrice || ' '} </Typography>
                        <Typography variant="subtitle1">  :</Typography>
                        <Typography>{totalPanelsWeights || ' '} </Typography>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="h6" gutterBottom></Typography>
                        <Typography>: {inverter?.producer || ' '} </Typography>
                        <Typography>: {inverter?.model || ' '}</Typography>
                        <Typography>: {inverter?.type || ' '}</Typography>
                        <Typography>: {inverter?.price || ' '} </Typography>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="h6" gutterBottom> </Typography>
                        <Typography>: {panel?.producer || ' '}</Typography>
                        <Typography>: {panel?.model || ' '}</Typography>
                        <Typography>: {panel?.type || ' '}</Typography>
                        <Typography>: {panel?.price || ' '} </Typography>
                        <Typography>: {panel?.orientation || ' '}</Typography>
                        <Typography> : {panel?.panel_type_connection || ' '}</Typography>
                        <Typography> : {panel?.number_panels_in_system || ' '}</Typography>
                        <Typography>. : {panel?.max_pv_power_for_params_kW || ' '} </Typography>
                        <Typography>: {panel?.voltage || ' '} </Typography>
                        <Typography>: {panel?.current || ' '} </Typography>
                    </Paper>
                </Grid>
            </Grid>

        </Box>
    );
};
